.table-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid $border-color;
  border-bottom: none;
  padding: .75rem;
}

.table-footer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  border: 1px solid $border-color;
  border-top: none;
  padding: .75rem .75rem 0;
  margin-top: 0;

  .pagination {
    margin-bottom: .75rem;
  }

}

.table {
  font-size: 12px;
  border: 1px solid $border-color;
  background-color: #fff;
  margin-bottom: 0;

  thead th {
    background-color: #f2f4f5;
  }

  th, td {
    vertical-align: middle !important;
  }

  tr th a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #000;
  }

  tr th a:hover {
    text-decoration: none;
  }
}
