// Please do not change the order of imports!!!

@import "custom";
@import "variables";

// Change this import to use a different color theme.
// To use the Classic Bootstrap Theme, remove the import.
//@import "src/app/bedag-styles/themes/bedag_ci_violet_theme";

@import "node_modules/bootstrap/scss/bootstrap";
@import "ng-select.theme";
@import "navigation";
@import "forms";
@import "header";
@import "tables";
@import "tabs";
@import "modal";

/* Typography */

html {
  font-size: 14px;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.6rem;
}

h4 {
  font-size: 1.4rem;
}

.lead {
  margin-bottom: 3rem;
  font-size: 1.2rem;
}

.border-bottom-dotted {
  border-bottom: dotted 1px;
}

.card {
  box-shadow: $box-shadow-sm;
}

.input-icon {
  position: absolute;
  top: .75rem;
  z-index: 3;
}

.input-icon.left-icon {
  left: 1rem;
}

.input-icon.right-icon {
  right: 1rem;
}

a.external:after {
  content: " \f35d";
  font-family: "Font Awesome 5 Free", serif;
  font-size: .8rem;
  font-weight: 900;
}

/* Handle close Navigation */
body.close-navigation {
  #alert-wrapper, #actionbar, #functionbar {
    width: 100%;
  }

  #actionbar {
    left: 0;
  }

  #navigation {
    margin-left: -$navigation-width;
  }

  #content {
    padding-left: $content-innenabstand;
  }
}

/* End close Navigation */

.title {
  font-weight: bold;
  font-size: 16px;
}

.title-divider {
  border-bottom: 1px solid;
  border-color: $text-muted;
  padding-bottom: 5px;
}

.title-divider.disabled {
  color: $text-muted;
}

a:not(:disabled):not(.disabled), button:not(:disabled):not(.disabled),
.btn:not(:disabled):not(.disabled), .dropdown-item:not(:disabled):not(.disabled),
.table-hover td:not(:disabled):not(.disabled), .list-group-item-action:not(:disabled):not(.disabled),
.cursor-pointer:not(:disabled):not(.disabled) {
  cursor: pointer !important;
}

.no-caret {
  &::after {
    content: none;
  }
}

/* Wrapper */
.content-wrapper {
  display: flex;
  align-items: stretch;
}

/* Alert Wrapper */
#alert-wrapper {
  position: fixed;
  top: $header-height - 1px;
  width: calc(100% - #{$navigation-width});
  height: auto;
  margin-left: -15px;
  background-color: rgba(255, 255, 255, 0.9);
  border-bottom: 1px solid $silver;
  box-shadow: 0 8px 8px -8px rgba(0, 0, 0, 0.2);
  transition: all 0.2s;
  z-index: 1000;
}

#alert-wrapper .alert {
  margin: 15px;
}

#alert-wrapper .alert:not(:last-child) {
  margin-bottom: 1rem !important;
}

.alert {
  display: flex;
  align-items: center;
}

/* Content */
#content {
  background-color: #fff;
  width: 100%;
  min-height: 100vh;
  padding: $content-innenabstand;
  padding-left: calc(#{$navigation-width} + #{$content-innenabstand});
  padding-top: $header-height;
  padding-bottom: $actionbar-container-height + 28px;
  transition: padding-left 0.2s;
}

#actionbar {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: fixed;
  width: calc(100% - #{$navigation-width});
  bottom: 0;
  left: $navigation-width;
  right: 0;
  padding: .75rem 15px !important;
  background-color: $light;
  border-top: 1px solid $silver;
  transition: all 0.2s;
  z-index: 100;
}

ol.breadcrumb {
  border-radius: 0;
  box-shadow: inset 0 -1px 0 0 $silver;
  font-size: 0.9rem;
  padding-left: 0;

  li.breadcrumb-item, li.breadcrumb-item a {
    color: $gunmetal;

    &.active {
      color: $dark;
      font-weight: bolder;

      &:before {
        font-weight: normal;
        color: $gunmetal;
      }
    }
  }
}

.code-content {
  background-color: rgba(0, 0, 0, 0.03);
}

.btn-with-badge {
  position: relative;
}

.btn-with-badge .badge {
  position: absolute !important;
  top: -10px !important;
  right: -12px !important;
}

.input-group-append {
  .btn-light {
    border-color: $input-border-color !important;
  }
}

/* Prettier Dropdowns */
select {
  @extend .custom-select;
}

/* Overflow Menu */
.overflow-menu .dropdown-toggle::after {
  display: none;
}

/* Datepicker Color Fix */
.ngb-dp-week-number, .ngb-dp-weekday {
  font-style: normal !important;
  color: $primary !important;
}

button.btn:not([class*="btn-"]) {
  @extend .btn-outline-primary;
}

/* Hotkey Keyboard Buttons */
.keyboard-key {
  background-color: #fbfbfb;
  font-family: monospace;
  border: 1px solid #aaa;
  border-radius: 0.2em;
  box-shadow: 0.1em 0.1em 0.2em rgba(0, 0, 0, 0.1);
  color: #222;
  padding: 0.1em 0.4em;
}

.upload-content {
  padding: 3rem;
  border: 2px dashed $border-color;
  border-radius: .75rem;
  transition: all ease-in .2s;

  span.fas, span.far, span.fa {
    transition: all .3s ease-in-out;
  }

  &:hover {
    border-color: $primary;
    box-shadow: 0 0 0 .3rem rgba($primary, 0.25);

    span.fas, span.far, span.fa {
      color: $primary !important;
    }

    span.fa-plus {
      transform: rotate(180deg);
    }
  }
}

label {
  font-weight: 600;

  &:not(.col-form-label) {
    margin-bottom: 0 !important;
  }

  &:not(.custom-control-label):not(.form-check-label) {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

dt {
  font-weight: 600;
}

.single-datepicker {
  width: calc(10em + 10px);
  padding-right: 5px;
  padding-left: 5px;
}

.required:after {
  content: "*";
  color: $danger;
  padding-left: .1rem;
}

.font-weight-600 {
  font-weight: 600;
}

.loading-overlay-base {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(245, 245, 245, .7);
  backdrop-filter: blur(2px);
  z-index: 9999;
}

.loading-overlay-full {
  @extend .loading-overlay-base;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loading-overlay-content {
  @extend .loading-overlay-base;
  top: $header-height;
  left: $navigation-width;
  width: calc(100% - #{$navigation-width});
  height: calc(100% - #{$header-height});
  transition: left 0.2s, width 0.2s;
}

body.close-navigation .loading-overlay-content {
  left: 0;
  width: 100%;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {

  .content-wrapper {
    flex-direction: column;
    align-items: flex-start;
  }

  #alert-wrapper {
    width: 100% !important;
  }

  #navigation {
    position: unset;
    overflow: unset;
    min-width: 100%;
    max-width: 100%;
    margin-left: 0 !important;
    padding-bottom: 0;
    border-right: none;
  }

  #functionbar, #actionbar {
    position: relative !important;
    width: calc(100% + 30px) !important;
    left: -15px;
  }

  #functionbar {
    top: 0 !important;
  }

  #actionbar {
    margin-top: 2rem;
    margin-bottom: -15px;
  }

  #content {
    padding-left: $content-innenabstand;
    padding-bottom: 15px !important;
  }

  body.close-navigation #navigation {
    display: none;
  }

  body:not(.close-navigation) {
    #content {
      padding-top: 0;
    }
  }

  .loading-overlay-content {
    width: 100%;
    left: 0;
  }

}
