@import "variables";

#header {
  height: $header-height;

  a.navbar-brand {
    color: white;
    display: flex;
    align-items: center;
  }

  ul.header-list {
    height: 100%;
  }

  ul.header-list .dropdown {
    height: 100%;
  }

  ul.header-list button {
    height: 100%;
    border-radius: 0;
    box-shadow: none;
  }

  .applogo {
    max-height: 30px;
  }

  .dropdown-toggle::after {
    display: none;
  }

}

.menu-toggler-btn {
  height: 100%;
  border-width: 0 1px 0 0;
  border-color: #222;
  box-shadow: none !important;
  border-radius: 0;
}

.user-menu {
  position: relative;
  bottom: .3rem;
}
