@import "variables";

$menu-item-height: 48px;
$submenu-item-height: 34px;

#navigation {
  position: fixed;
  height: 100%;
  overflow-y: auto;
  min-width: $navigation-width;
  max-width: $navigation-width;
  padding-top: $header-height;
  padding-bottom: 20px;
  border-right: 1px solid $silver;
  z-index: 200;
  background-color: $light;
  transition: all 0.2s;

  .nav-item:first-child a.vertical-menu-item {
    border-top: none;
  }
}

#navmenu {
  border-bottom: 1px solid $silver;
}

.heading {
  cursor: pointer;
  position: relative;
  font-weight: 600;
}

.heading-children {
  &.collapsed {
    display: none;
  }

  &.expanded {
    display: block;
    border-bottom: 1px solid $silver;
    margin-bottom: -1px;
  }
}

.vertical-menu-item {
  display: flex;
  align-items: center;
  color: black;
  cursor: pointer;
  padding: 0 .75rem;
  text-decoration: none;

  &.selected {
    background-color: rgba($primary, .15);
  }

  &:hover {
    background-color: rgba($primary, .05);
    text-decoration: none;
    color: black;
  }

  &.main {
    border-bottom: 1px solid $silver;
    font-weight: 600;
    height: $menu-item-height;
    line-height: $menu-item-height;
  }

  &.main.expanded {
    border-bottom: 1px solid $silver;
  }

  &.level-2 {
    line-height: $submenu-item-height;
    padding-left: 2.6rem;
  }

  &.level-3 {
    line-height: $submenu-item-height;
    padding-left: 4.6rem;
  }

  &.level-4 {
    line-height: $submenu-item-height;
    padding-left: 6.6rem;
  }
}

.nav-item-icon {
  color: $dark;
}

:not(.no-icons) .icon-margin {
  @extend .ml-2;
}

.no-icons .nav-item-icon {
  display: none !important;
}

.nav-item-expand-icon {
  color: $primary;
  font-size: .8rem;
}

button.vertical-menu-item {
  border: none;
  background-color: transparent;
  margin: 0;

  &:focus {
    outline: none;
  }
}

.level-1.expanded .fa-chevron-right, .level-1:not(.expanded) .fa-chevron-right,
.level-2.expanded .fa-chevron-right, .level-2:not(.expanded) .fa-chevron-right {
  transition: transform .1s ease-in-out;
}

.level-1.expanded .fa-chevron-right, .level-2.expanded .fa-chevron-right {
  transform: rotate(90deg);
}

.level-1:not(.expanded) .fa-chevron-right, .level-2:not(.expanded) .fa-chevron-right,
.level-3:not(.expanded) .fa-chevron-right {
  transform: rotate(0deg);
}

