// Add our styling to the ng-select default theme
@import "~@ng-select/ng-select/themes/default.theme.css";

.ng-select {
  .ng-select-container {
    height: auto;
    min-height: calc(1.5em + 0.75rem + 2px);
    padding: 0;
    border-radius: $input-border-radius;
    border: $input-border-width solid $input-border-color;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

    background: #fff url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5' viewBox='0 0 4 5'%3e%3cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e") right .75rem center/8px 10px no-repeat !important;

    &:hover {
      box-shadow: none;
    }

  }

  &.ng-select-focused:not(.ng-select-opened) > .ng-select-container {
    border-color: $input-focus-border-color;
    box-shadow: $input-focus-box-shadow;
  }

  &.is-invalid {
    .ng-select-container {
      border-color: $danger !important;
    }

    &.ng-select-focused:not(.ng-select-opened) > .ng-select-container {
      box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25) !important;
    }
  }

  &.ng-select-disabled > .ng-select-container {
    background-color: $input-disabled-bg;
  }

  .ng-arrow-wrapper {
    opacity: 0;
  }

  &.ng-select-single .ng-select-container {
    height: calc(1.5em + 0.75rem + 2px);
  }

  &.ng-select-multiple .ng-select-container .ng-value-container .ng-value {
    background-color: rgba($primary, .1);

    .ng-value-icon:hover {
      background-color: rgba($primary, .2);
    }
  }

  .ng-dropdown-panel {
    .ng-dropdown-panel-items {
      .ng-option {
        &.ng-option-marked, &.ng-option-selected {
          background-color: rgba($primary, .1);
        }
      }
    }
  }

}
