@import "~prismjs/themes/prism-okaidia.css";

a.highlight:before {
  @extend .fas;
  @extend .fa-angle-right;
  @extend .fa-fw;
}

ul.related-elements {
  list-style: none;
  padding-left: 1.5em;

  li :first-child:before {
    @extend .fas;
    @extend .fa-angle-right;
    @extend .fa-fw;
    padding-right: 0.3em;
  }

}

.autocomplete-result {
  min-width: 20em;
}
